/**
 * Database - Dexie.js IndexedDB wrapper
 * Based on data-model.md schema
 */

import Dexie, { type Table } from 'dexie';
import type { AnalysisSession } from '../types/session';
import type { Page } from '../types/analysis';
import type { RecognizedComponent } from '../types/component';
import type { Menu } from '../types/menu';
import type { Project, ProjectFile } from '../types/project';

/**
 * Admin Clone database schema
 */
export class AdminCloneDB extends Dexie {
  sessions!: Table<AnalysisSession, string>;
  pages!: Table<Page, string>;
  components!: Table<RecognizedComponent & { pageId: string }, string>;
  menus!: Table<Menu, string>;
  projects!: Table<Project, string>;
  projectFiles!: Table<ProjectFile, string>;

  constructor() {
    super('AdminCloneDB');
    
    this.version(1).stores({
      // Sessions table - indexed by id, rootUrl, status
      sessions: 'id, rootUrl, status, createdAt, updatedAt',
      
      // Pages table - indexed by id, sessionId, url, status
      pages: 'id, sessionId, url, status, analyzedAt',
      
      // Components table - indexed by id, pageId, type
      // Note: components are stored with pageId for queries
      components: 'id, pageId, type',
      
      // Menus table - indexed by id, sessionId
      menus: 'id, sessionId, extractedAt',
      
      // Projects table - indexed by id, sessionId
      projects: 'id, sessionId, createdAt',
      
      // Project files table - indexed by id, projectId, path
      projectFiles: 'id, projectId, path, type',
    });
  }
}

/**
 * Singleton database instance
 */
export const db = new AdminCloneDB();

/**
 * Clear all data from the database
 */
export async function clearDatabase(): Promise<void> {
  await db.transaction('rw', [db.sessions, db.pages, db.components, db.menus, db.projects, db.projectFiles], async () => {
    await db.sessions.clear();
    await db.pages.clear();
    await db.components.clear();
    await db.menus.clear();
    await db.projects.clear();
    await db.projectFiles.clear();
  });
}

/**
 * Generate a unique ID
 */
export function generateId(): string {
  return `${Date.now()}-${Math.random().toString(36).substring(2, 9)}`;
}
