/**
 * Page Repository - CRUD operations for analyzed pages
 */

import { db, generateId } from './db';
import type { Page, PageStatus } from '../types/analysis';

/**
 * Create a new page
 */
export async function createPage(page: Omit<Page, 'id'>): Promise<Page> {
  const newPage: Page = {
    ...page,
    id: generateId(),
  };
  
  await db.pages.add(newPage);
  return newPage;
}

/**
 * Get a page by ID
 */
export async function getPage(id: string): Promise<Page | undefined> {
  return db.pages.get(id);
}

/**
 * Get a page by URL and session
 */
export async function getPageByUrl(sessionId: string, url: string): Promise<Page | undefined> {
  return db.pages
    .where('sessionId')
    .equals(sessionId)
    .filter(page => page.url === url)
    .first();
}

/**
 * Update a page
 */
export async function updatePage(id: string, updates: Partial<Page>): Promise<Page | undefined> {
  const page = await db.pages.get(id);
  if (!page) {
    return undefined;
  }
  
  const updated: Page = {
    ...page,
    ...updates,
  };
  
  await db.pages.put(updated);
  return updated;
}

/**
 * Delete a page and its components
 */
export async function deletePage(id: string): Promise<boolean> {
  try {
    await db.transaction('rw', db.pages, db.components, async () => {
      // Delete components for this page
      await db.components.where('pageId').equals(id).delete();
      // Delete the page
      await db.pages.delete(id);
    });
    return true;
  } catch (error) {
    console.error('[PageRepository] Failed to delete page:', error);
    return false;
  }
}

/**
 * Get all pages for a session
 */
export async function getPagesBySession(sessionId: string): Promise<Page[]> {
  return db.pages.where('sessionId').equals(sessionId).toArray();
}

/**
 * Get pages by status for a session
 */
export async function getPagesByStatus(sessionId: string, status: PageStatus): Promise<Page[]> {
  return db.pages
    .where('sessionId')
    .equals(sessionId)
    .filter(page => page.status === status)
    .toArray();
}

/**
 * Update page status
 */
export async function updatePageStatus(id: string, status: PageStatus, error?: string): Promise<boolean> {
  const updates: Partial<Page> = { status };
  if (status === 'completed') {
    updates.analyzedAt = Date.now();
  }
  if (error) {
    updates.error = error;
  }
  
  const result = await updatePage(id, updates);
  return result !== undefined;
}

/**
 * Check if a URL has been analyzed in a session
 */
export async function isUrlAnalyzed(sessionId: string, url: string): Promise<boolean> {
  const page = await getPageByUrl(sessionId, url);
  return page?.status === 'completed';
}

/**
 * Get count of pages by status for a session
 */
export async function getPageCountByStatus(sessionId: string): Promise<Record<PageStatus, number>> {
  const pages = await getPagesBySession(sessionId);
  
  const counts: Record<PageStatus, number> = {
    pending: 0,
    analyzing: 0,
    completed: 0,
    failed: 0,
    skipped: 0,
  };
  
  for (const page of pages) {
    counts[page.status]++;
  }
  
  return counts;
}

/**
 * Get the next pending page for analysis
 */
export async function getNextPendingPage(sessionId: string): Promise<Page | undefined> {
  return db.pages
    .where('sessionId')
    .equals(sessionId)
    .filter(page => page.status === 'pending')
    .first();
}

/**
 * Bulk create pages
 */
export async function createPages(pages: Array<Omit<Page, 'id'>>): Promise<Page[]> {
  const newPages: Page[] = pages.map(page => ({
    ...page,
    id: generateId(),
  }));
  
  await db.pages.bulkAdd(newPages);
  return newPages;
}
