// Admin Clone Extension - Background Service Worker
// This service worker handles extension lifecycle events and message passing

import { 
  initMessageListener, 
  registerHandler 
} from '@core/messaging/message-handler';
import type { 
  PingMessage, 
  AnalyzePageMessage,
  ExtensionMessage 
} from '@core/messaging/messages';
import type { Menu, MenuItem } from '@core/types/menu';
import type { Page } from '@core/types/analysis';
import type { InteractionTarget } from '@core/types/interaction';
import { 
  getSessionSummaries, 
  deleteSession as deleteSessionFromDb,
  renameSession as renameSessionFromDb
} from '@core/storage/session-repository';
import { getPagesBySession } from '@core/storage/page-repository';
import { cleanupOldSessions, getStorageStats } from '@core/storage/storage-cleanup';

const SIDE_PANEL_CONTEXT_MENU_ID = 'admin-clone-open-sidepanel';
const SIDE_PANEL_CONTEXT_MENU_TITLE = ' Admin Clone';

console.log('[Admin Clone] Background service worker initialized');

// Run storage cleanup on startup
cleanupOldSessions(10).then(count => {
  if (count > 0) {
    console.log(`[Admin Clone] Cleaned up ${count} old sessions on startup`);
  }
});

function registerContextMenus() {
  if (!chrome?.contextMenus) {
    return;
  }
  chrome.contextMenus.removeAll(() => {
    // Ignore errors when there are no existing items
    chrome.contextMenus.create(
      {
        id: SIDE_PANEL_CONTEXT_MENU_ID,
        title: SIDE_PANEL_CONTEXT_MENU_TITLE,
        contexts: ['page'],
      },
      () => {
        const error = chrome.runtime.lastError;
        if (error) {
          console.warn('[Admin Clone] Context menu registration warning:', error.message);
        }
      }
    );
  });
}

async function ensureSidePanelEnabled(tab: chrome.tabs.Tab | undefined): Promise<boolean> {
  if (!tab || tab.id === undefined || !chrome.sidePanel) {
    return false;
  }
  try {
    if (typeof chrome.sidePanel.setOptions === 'function') {
      await chrome.sidePanel.setOptions({
        tabId: tab.id,
        path: 'sidepanel.html',
        enabled: true,
      });
      return true;
    }
    if (typeof chrome.sidePanel.setPanelBehavior === 'function') {
      await chrome.sidePanel.setPanelBehavior({ openPanelOnActionClick: true });
      return true;
    }
  } catch (error) {
    console.warn('[Admin Clone] Failed to enable side panel for tab', tab.id, error);
  }
  return false;
}

async function resolveTargetTab(provided?: chrome.tabs.Tab): Promise<chrome.tabs.Tab | undefined> {
  if (provided?.id !== undefined) {
    return provided;
  }
  const [activeTab] = await chrome.tabs.query({ active: true, currentWindow: true });
  return activeTab;
}

function buildSidePanelOpenOptions(tab: chrome.tabs.Tab | undefined): chrome.sidePanel.OpenOptions | null {
  if (!tab) return null;
  if (tab.id !== undefined) {
    return { tabId: tab.id };
  }
  if (tab.windowId !== undefined) {
    return { windowId: tab.windowId };
  }
  return null;
}

function getInfoTabId(info?: chrome.contextMenus.OnClickData): number | undefined {
  if (!info) return undefined;
  const candidate = (info as chrome.contextMenus.OnClickData & { tabId?: number }).tabId;
  return typeof candidate === 'number' ? candidate : undefined;
}

function buildImmediateOpenOptions(info?: chrome.contextMenus.OnClickData, tab?: chrome.tabs.Tab): chrome.sidePanel.OpenOptions | null {
  const infoTabId = getInfoTabId(info);
  if (infoTabId !== undefined && infoTabId >= 0) {
    return { tabId: infoTabId };
  }
  if (tab?.id !== undefined) {
    return { tabId: tab.id };
  }
  if (tab?.windowId !== undefined) {
    return { windowId: tab.windowId };
  }
  return null;
}

async function fallbackOpenSidePanel(tab?: chrome.tabs.Tab) {
  try {
    const targetTab = await resolveTargetTab(tab);
    if (!targetTab) {
      console.warn('[Admin Clone] Unable to open side panel: no active tab');
      return;
    }
    await ensureSidePanelEnabled(targetTab);
    const options = buildSidePanelOpenOptions(targetTab);
    if (!options) {
      console.warn('[Admin Clone] Unable to open side panel: missing tab/window info');
      return;
    }
    if (!chrome?.sidePanel?.open) {
      console.warn('[Admin Clone] Side panel API unavailable during fallback open');
      return;
    }
    await chrome.sidePanel.open(options);
  } catch (error) {
    console.error('[Admin Clone] Failed to open side panel after fallback', error);
  }
}

function handleContextMenuClick(info: chrome.contextMenus.OnClickData, tab?: chrome.tabs.Tab) {
  if (!chrome?.sidePanel?.open) {
    console.warn('[Admin Clone] Side panel API unavailable when handling context menu click');
    return;
  }
  const immediateOptions = buildImmediateOpenOptions(info, tab);
  if (!immediateOptions) {
    console.warn('[Admin Clone] Unable to open side panel: missing tab/window info');
    return;
  }
  chrome.sidePanel.open(immediateOptions).catch(error => {
    const message = error instanceof Error ? error.message : String(error);
    if (message.includes('user gesture')) {
      console.error('[Admin Clone] Side panel open blocked: user gesture required', error);
      return;
    }
    console.warn('[Admin Clone] Immediate side panel open failed, attempting fallback', error);
    fallbackOpenSidePanel(tab).catch(fallbackError => {
      console.error('[Admin Clone] Fallback side panel open failed', fallbackError);
    });
  });
}

chrome.runtime.onInstalled.addListener(() => {
  registerContextMenus();
});

chrome.runtime.onStartup.addListener(() => {
  registerContextMenus();
});

// Ensure context menu exists even if service worker is reloaded outside of install/startup.
registerContextMenus();

chrome.contextMenus?.onClicked.addListener((info, tab) => {
  if (info.menuItemId === SIDE_PANEL_CONTEXT_MENU_ID) {
    handleContextMenuClick(info, tab);
  }
});

// Traversal state management
interface TraversalState {
  id: string;
  tabId: number;
  menu: Menu;
  status: 'running' | 'paused' | 'completed' | 'cancelled' | 'error';
  navigableItems: MenuItem[];
  currentIndex: number;
  analyzedPages: Page[];
  failedItems: MenuItem[];
  startTime: number;
}

const activeTraversals = new Map<string, TraversalState>();

const MAX_INTERACTIONS_PER_PAGE = 5;

// Keep service worker alive during traversal
let keepAliveInterval: ReturnType<typeof setInterval> | null = null;

function startKeepAlive() {
  if (keepAliveInterval) return;
  keepAliveInterval = setInterval(() => {
    console.log('[Admin Clone] Keep alive ping');
  }, 20000); // Every 20 seconds
}

function stopKeepAlive() {
  if (keepAliveInterval) {
    clearInterval(keepAliveInterval);
    keepAliveInterval = null;
  }
}

// Helper to get navigable items from menu
function getNavigableItems(menu: Menu): MenuItem[] {
  const items: MenuItem[] = [];
  function collect(menuItems: MenuItem[]) {
    for (const item of menuItems) {
      if (item.path && !item.path.startsWith('#') && !item.path.startsWith('javascript:')) {
        items.push(item);
      }
      if (item.children) {
        collect(item.children);
      }
    }
  }
  collect(menu.items);
  return items;
}

// Emit traversal progress to popup
function emitTraversalProgress(traversal: TraversalState) {
  const progress = {
    status: traversal.status,
    totalItems: traversal.navigableItems.length,
    completedItems: traversal.currentIndex,
    currentItem: traversal.navigableItems[traversal.currentIndex] || null,
    failedItems: traversal.failedItems,
    startTime: traversal.startTime,
    elapsedTime: Date.now() - traversal.startTime,
    estimatedRemainingTime: calculateRemainingTime(traversal),
  };
  
  chrome.runtime.sendMessage({
    type: 'TRAVERSAL_PROGRESS',
    traversalId: traversal.id,
    progress,
  }).catch(() => {}); // Ignore if popup is closed
}

function calculateRemainingTime(traversal: TraversalState): number {
  if (traversal.currentIndex === 0) return 0;
  const elapsed = Date.now() - traversal.startTime;
  const avgTimePerItem = elapsed / traversal.currentIndex;
  const remaining = traversal.navigableItems.length - traversal.currentIndex;
  return avgTimePerItem * remaining;
}

// Process next menu item in traversal
async function processNextItem(traversalId: string) {
  const traversal = activeTraversals.get(traversalId);
  if (!traversal || traversal.status !== 'running') return;
  
  if (traversal.currentIndex >= traversal.navigableItems.length) {
    // Traversal complete
    traversal.status = 'completed';
    emitTraversalProgress(traversal);
    
    chrome.runtime.sendMessage({
      type: 'TRAVERSAL_COMPLETED',
      traversalId,
      pages: traversal.analyzedPages,
    }).catch(() => {});
    
    activeTraversals.delete(traversalId);
    if (activeTraversals.size === 0) stopKeepAlive();
    return;
  }
  
  const menuItem = traversal.navigableItems[traversal.currentIndex];
  emitTraversalProgress(traversal);
  
  try {
    // Send navigation command to content script
    const navResponse = await chrome.tabs.sendMessage(traversal.tabId, {
      type: 'TRAVERSE_NAVIGATE',
      menuItem,
    });
    
    if (!navResponse?.success) {
      throw new Error(navResponse?.error || 'Navigation failed');
    }
    
    // Wait for page to load and analyze
    await new Promise(resolve => setTimeout(resolve, 1000));
    
    // Request page analysis
    const analysisResponse = await chrome.tabs.sendMessage(traversal.tabId, {
      type: 'ANALYZE_PAGE',
      sessionId: traversalId,
    });
    
    if (analysisResponse?.result?.success && analysisResponse.result.page) {
      traversal.analyzedPages.push(analysisResponse.result.page);
      
      chrome.runtime.sendMessage({
        type: 'TRAVERSAL_PAGE_ANALYZED',
        traversalId,
        page: analysisResponse.result.page,
        menuItem,
      }).catch(() => {});

      await triggerInteractionVariants(traversal, menuItem);
    }
  } catch (error) {
    console.error('[Traversal] Failed to process item:', menuItem.title, error);
    traversal.failedItems.push(menuItem);
    
    chrome.runtime.sendMessage({
      type: 'TRAVERSAL_ERROR',
      traversalId,
      error: error instanceof Error ? error.message : 'Unknown error',
      menuItem,
    }).catch(() => {});
  }
  
  // Move to next item
  traversal.currentIndex++;
  
  // Schedule next item processing
  if (traversal.status === 'running') {
    setTimeout(() => processNextItem(traversalId), 500);
  }
}

async function triggerInteractionVariants(traversal: TraversalState, menuItem: MenuItem): Promise<void> {
  try {
    const targetsResponse = await chrome.tabs.sendMessage(traversal.tabId, {
      type: 'COLLECT_INTERACTIONS',
      limit: MAX_INTERACTIONS_PER_PAGE,
    });
    const targets: InteractionTarget[] = targetsResponse?.result?.success ? targetsResponse.result.targets : [];
    if (!targets || targets.length === 0) {
      return;
    }
    for (const target of targets) {
      const runResponse = await chrome.tabs.sendMessage(traversal.tabId, {
        type: 'RUN_INTERACTION',
        targetId: target.id,
      });
      if (!runResponse?.result?.success) {
        continue;
      }
      await new Promise(resolve => setTimeout(resolve, 200));
      const variantResponse = await chrome.tabs.sendMessage(traversal.tabId, {
        type: 'ANALYZE_PAGE',
        sessionId: traversal.id,
        interactionContext: {
          targetId: target.id,
          type: target.type,
          label: target.label,
          group: target.group,
          groupLabel: target.groupLabel,
          source: target.source,
        },
      });
      if (variantResponse?.result?.success && variantResponse.result.page) {
        traversal.analyzedPages.push(variantResponse.result.page);
        chrome.runtime.sendMessage({
          type: 'TRAVERSAL_PAGE_ANALYZED',
          traversalId: traversal.id,
          page: variantResponse.result.page,
          menuItem,
        }).catch(() => {});
      }
    }
  } catch (error) {
    console.warn('[Traversal] Interaction variant processing failed:', error);
  }
}

// Register message handlers
registerHandler<PingMessage>('PING', (_message, _sender, sendResponse) => {
  sendResponse({ type: 'PONG', timestamp: Date.now() });
  return true;
});

registerHandler<AnalyzePageMessage>('ANALYZE_PAGE', async (message, sender) => {
  console.log('[Admin Clone] ANALYZE_PAGE received', message, 'from tab:', sender.tab?.id);
  
  // Forward to content script if from popup
  if (sender.tab?.id) {
    // Message came from content script
    return {
      type: 'ANALYSIS_RESULT',
      result: {
        success: true,
        page: {
          url: sender.url || '',
          title: '',
          status: 'pending',
        },
      },
    } as ExtensionMessage;
  }
  
  // Message came from popup - forward to active tab's content script
  const [tab] = await chrome.tabs.query({ active: true, currentWindow: true });
  if (tab?.id) {
    try {
      const response = await chrome.tabs.sendMessage(tab.id, message);
      return response;
    } catch (error) {
      console.error('[Admin Clone] Failed to send to content script:', error);
      return {
        type: 'ANALYSIS_RESULT',
        result: {
          success: false,
          error: 'Failed to communicate with page. Please refresh and try again.',
        },
      } as ExtensionMessage;
    }
  }
  
  return {
    type: 'ANALYSIS_RESULT',
    result: {
      success: false,
      error: 'No active tab found',
    },
  } as ExtensionMessage;
});

// Initialize message listener
initMessageListener();

// Traversal message handlers (outside of registerHandler for direct handling)
chrome.runtime.onMessage.addListener((message, _sender, sendResponse) => {
  switch (message.type) {
    case 'START_TRAVERSAL': {
      const { traversalId, tabId, menu } = message;
      
      const navigableItems = getNavigableItems(menu);
      const traversal: TraversalState = {
        id: traversalId,
        tabId,
        menu,
        status: 'running',
        navigableItems,
        currentIndex: 0,
        analyzedPages: [],
        failedItems: [],
        startTime: Date.now(),
      };
      
      activeTraversals.set(traversalId, traversal);
      startKeepAlive();
      
      console.log('[Admin Clone] Starting traversal:', traversalId, 'Items:', navigableItems.length);
      
      // Start processing
      processNextItem(traversalId);
      
      sendResponse({ success: true, totalItems: navigableItems.length });
      return true;
    }
    
    case 'PAUSE_TRAVERSAL': {
      const traversal = activeTraversals.get(message.traversalId);
      if (traversal) {
        traversal.status = 'paused';
        emitTraversalProgress(traversal);
        console.log('[Admin Clone] Traversal paused:', message.traversalId);
      }
      sendResponse({ success: true });
      return true;
    }
    
    case 'RESUME_TRAVERSAL': {
      const traversal = activeTraversals.get(message.traversalId);
      if (traversal && traversal.status === 'paused') {
        traversal.status = 'running';
        emitTraversalProgress(traversal);
        processNextItem(message.traversalId);
        console.log('[Admin Clone] Traversal resumed:', message.traversalId);
      }
      sendResponse({ success: true });
      return true;
    }
    
    case 'STOP_TRAVERSAL': {
      const traversal = activeTraversals.get(message.traversalId);
      if (traversal) {
        traversal.status = 'cancelled';
        emitTraversalProgress(traversal);
        activeTraversals.delete(message.traversalId);
        
        if (activeTraversals.size === 0) stopKeepAlive();
        console.log('[Admin Clone] Traversal stopped:', message.traversalId);
      }
      sendResponse({ success: true });
      return true;
    }
    
    case 'GET_TRAVERSAL_STATUS': {
      const traversal = activeTraversals.get(message.traversalId);
      if (traversal) {
        sendResponse({
          success: true,
          status: traversal.status,
          progress: {
            totalItems: traversal.navigableItems.length,
            completedItems: traversal.currentIndex,
            failedItems: traversal.failedItems.length,
          },
        });
      } else {
        sendResponse({ success: false, error: 'Traversal not found' });
      }
      return true;
    }
    
    // Session Management handlers
    case 'GET_SESSIONS': {
      getSessionSummaries()
        .then(sessions => sendResponse({ success: true, data: sessions }))
        .catch(error => sendResponse({ success: false, error: error.message }));
      return true;
    }
    
    case 'GET_SESSION_PAGES': {
      getPagesBySession(message.sessionId)
        .then(pages => sendResponse({ success: true, data: pages }))
        .catch(error => sendResponse({ success: false, error: error.message }));
      return true;
    }
    
    case 'DELETE_SESSION': {
      console.log('[Admin Clone] Delete session:', message.sessionId);
      deleteSessionFromDb(message.sessionId)
        .then(success => {
          if (success) {
            // Broadcast storage update
            chrome.runtime.sendMessage({ type: 'STORAGE_UPDATE', action: 'session_deleted' }).catch(() => {});
          }
          sendResponse({ success });
        })
        .catch(error => sendResponse({ success: false, error: error.message }));
      return true;
    }
    
    case 'RENAME_SESSION': {
      console.log('[Admin Clone] Rename session:', message.sessionId, '->', message.name);
      renameSessionFromDb(message.sessionId, message.name)
        .then(success => {
          if (success) {
            chrome.runtime.sendMessage({ type: 'STORAGE_UPDATE', action: 'session_renamed' }).catch(() => {});
          }
          sendResponse({ success });
        })
        .catch(error => sendResponse({ success: false, error: error.message }));
      return true;
    }
    
    case 'GET_STORAGE_STATS': {
      getStorageStats()
        .then(stats => sendResponse({ success: true, data: stats }))
        .catch(error => sendResponse({ success: false, error: error.message }));
      return true;
    }
    
    case 'CLEANUP_STORAGE': {
      cleanupOldSessions(message.keepCount || 10)
        .then(count => {
          if (count > 0) {
            chrome.runtime.sendMessage({ type: 'STORAGE_UPDATE', action: 'cleanup' }).catch(() => {});
          }
          sendResponse({ success: true, deletedCount: count });
        })
        .catch(error => sendResponse({ success: false, error: error.message }));
      return true;
    }
  }
  
  return false;
});

// Listen for extension installation
chrome.runtime.onInstalled.addListener((details) => {
  console.log('[Admin Clone] Extension installed', details.reason);
});

export {};
