/**
 * Message Handler - Framework for handling extension messages in background
 */

import type { ExtensionMessage, MessageType } from './messages';

/**
 * Message handler function type
 */
export type MessageHandler<T extends ExtensionMessage = ExtensionMessage> = (
  message: T,
  sender: chrome.runtime.MessageSender,
  sendResponse: (response: ExtensionMessage) => void
) => boolean | void | Promise<ExtensionMessage>;

/**
 * Registry of message handlers
 */
const handlers = new Map<MessageType, MessageHandler>();

/**
 * Register a message handler
 */
export function registerHandler<T extends ExtensionMessage>(
  type: MessageType,
  handler: MessageHandler<T>
): void {
  handlers.set(type, handler as MessageHandler);
  console.log(`[MessageHandler] Registered handler for: ${type}`);
}

/**
 * Unregister a message handler
 */
export function unregisterHandler(type: MessageType): void {
  handlers.delete(type);
  console.log(`[MessageHandler] Unregistered handler for: ${type}`);
}

/**
 * Handle incoming message
 */
export function handleMessage(
  message: ExtensionMessage,
  sender: chrome.runtime.MessageSender,
  sendResponse: (response: ExtensionMessage) => void
): boolean {
  const handler = handlers.get(message.type);
  
  if (!handler) {
    console.warn(`[MessageHandler] No handler for message type: ${message.type}`);
    return false;
  }
  
  console.log(`[MessageHandler] Handling message: ${message.type}`, message);
  
  try {
    const result = handler(message, sender, sendResponse);
    
    // If handler returns a promise, handle it
    if (result instanceof Promise) {
      result
        .then(response => {
          sendResponse(response);
        })
        .catch(error => {
          console.error(`[MessageHandler] Error handling ${message.type}:`, error);
          sendResponse({
            type: message.type,
            error: error.message,
          } as ExtensionMessage);
        });
      return true; // Keep channel open for async response
    }
    
    // Return true if handler wants to send async response
    return result === true;
  } catch (error) {
    console.error(`[MessageHandler] Error handling ${message.type}:`, error);
    return false;
  }
}

/**
 * Initialize message listener
 */
export function initMessageListener(): void {
  chrome.runtime.onMessage.addListener(handleMessage);
  console.log('[MessageHandler] Message listener initialized');
}

/**
 * Remove message listener
 */
export function removeMessageListener(): void {
  chrome.runtime.onMessage.removeListener(handleMessage);
  console.log('[MessageHandler] Message listener removed');
}
