/**
 * Session Repository - CRUD operations for analysis sessions
 */

import { db, generateId } from './db';
import type { 
  AnalysisSession, 
  CreateSessionParams, 
  UpdateSessionParams,
  SessionSummary,
  SessionStatus 
} from '../types/session';

/**
 * Create a new analysis session
 */
export async function createSession(params: CreateSessionParams): Promise<AnalysisSession> {
  const now = Date.now();
  const session: AnalysisSession = {
    id: generateId(),
    name: params.name || `Session ${new Date().toLocaleString('zh-CN')}`,
    rootUrl: params.rootUrl,
    status: 'idle',
    totalPages: 0,
    analyzedPages: 0,
    failedPages: 0,
    createdAt: now,
    updatedAt: now,
  };
  
  await db.sessions.add(session);
  return session;
}

/**
 * Get a session by ID
 */
export async function getSession(id: string): Promise<AnalysisSession | undefined> {
  return db.sessions.get(id);
}

/**
 * Update a session
 */
export async function updateSession(params: UpdateSessionParams): Promise<AnalysisSession | undefined> {
  const session = await db.sessions.get(params.id);
  if (!session) {
    return undefined;
  }
  
  const updated: AnalysisSession = {
    ...session,
    ...params,
    updatedAt: Date.now(),
  };
  
  // Set completedAt if status is completed
  if (params.status === 'completed' && !updated.completedAt) {
    updated.completedAt = Date.now();
  }
  
  await db.sessions.put(updated);
  return updated;
}

/**
 * Delete a session and all related data
 */
export async function deleteSession(id: string): Promise<boolean> {
  try {
    await db.transaction('rw', [db.sessions, db.pages, db.components, db.menus, db.projects, db.projectFiles], async () => {
      // Get all pages for this session
      const pages = await db.pages.where('sessionId').equals(id).toArray();
      const pageIds = pages.map(p => p.id);
      
      // Delete components for these pages
      await db.components.where('pageId').anyOf(pageIds).delete();
      
      // Delete pages
      await db.pages.where('sessionId').equals(id).delete();
      
      // Delete menus
      await db.menus.where('sessionId').equals(id).delete();
      
      // Get projects for this session
      const projects = await db.projects.where('sessionId').equals(id).toArray();
      const projectIds = projects.map(p => p.id);
      
      // Delete project files
      await db.projectFiles.where('projectId').anyOf(projectIds).delete();
      
      // Delete projects
      await db.projects.where('sessionId').equals(id).delete();
      
      // Finally delete the session
      await db.sessions.delete(id);
    });
    
    return true;
  } catch (error) {
    console.error('[SessionRepository] Failed to delete session:', error);
    return false;
  }
}

/**
 * Get all sessions
 */
export async function getAllSessions(): Promise<AnalysisSession[]> {
  return db.sessions.orderBy('updatedAt').reverse().toArray();
}

/**
 * Get sessions by status
 */
export async function getSessionsByStatus(status: SessionStatus): Promise<AnalysisSession[]> {
  return db.sessions.where('status').equals(status).toArray();
}

/**
 * Get session summaries for list display
 */
export async function getSessionSummaries(): Promise<SessionSummary[]> {
  const sessions = await getAllSessions();
  
  return sessions.map(session => ({
    id: session.id,
    name: session.name,
    rootUrl: session.rootUrl,
    status: session.status,
    progress: session.totalPages > 0 
      ? Math.round((session.analyzedPages / session.totalPages) * 100)
      : 0,
    totalPages: session.totalPages,
    analyzedPages: session.analyzedPages,
    createdAt: session.createdAt,
    updatedAt: session.updatedAt,
  }));
}

/**
 * Update session status
 */
export async function updateSessionStatus(id: string, status: SessionStatus): Promise<boolean> {
  const result = await updateSession({ id, status });
  return result !== undefined;
}

/**
 * Increment analyzed pages count
 */
export async function incrementAnalyzedPages(id: string, failed = false): Promise<void> {
  const session = await getSession(id);
  if (!session) return;
  
  await updateSession({
    id,
    analyzedPages: session.analyzedPages + (failed ? 0 : 1),
    failedPages: session.failedPages + (failed ? 1 : 0),
  });
}

/**
 * Set total pages count
 */
export async function setTotalPages(id: string, totalPages: number): Promise<void> {
  await updateSession({ id, totalPages });
}

/**
 * Get recent sessions (last 10)
 */
export async function getRecentSessions(limit = 10): Promise<AnalysisSession[]> {
  return db.sessions.orderBy('updatedAt').reverse().limit(limit).toArray();
}

/**
 * Rename a session
 */
export async function renameSession(id: string, name: string): Promise<boolean> {
  const result = await updateSession({ id, name });
  return result !== undefined;
}
