/**
 * Storage Cleanup - Automatic cleanup of old sessions
 */

import { db } from './db';
import { deleteSession, getAllSessions } from './session-repository';

/**
 * Maximum number of sessions to keep
 */
const MAX_SESSIONS = 10;

/**
 * Clean up old sessions, keeping only the most recent ones
 * @param keepCount Number of sessions to keep (default: 10)
 * @returns Number of sessions deleted
 */
export async function cleanupOldSessions(keepCount = MAX_SESSIONS): Promise<number> {
  try {
    const allSessions = await getAllSessions();
    
    // Sessions are already sorted by updatedAt desc
    if (allSessions.length <= keepCount) {
      console.log('[StorageCleanup] No cleanup needed, session count:', allSessions.length);
      return 0;
    }
    
    // Get sessions to delete (older ones beyond keepCount)
    const sessionsToDelete = allSessions.slice(keepCount);
    
    console.log(`[StorageCleanup] Cleaning up ${sessionsToDelete.length} old sessions`);
    
    let deletedCount = 0;
    for (const session of sessionsToDelete) {
      // Skip sessions that are currently active
      if (session.status === 'analyzing' || session.status === 'traversing') {
        console.log(`[StorageCleanup] Skipping active session: ${session.id}`);
        continue;
      }
      
      const success = await deleteSession(session.id);
      if (success) {
        deletedCount++;
        console.log(`[StorageCleanup] Deleted session: ${session.id} (${session.name})`);
      }
    }
    
    console.log(`[StorageCleanup] Cleanup complete, deleted ${deletedCount} sessions`);
    return deletedCount;
  } catch (error) {
    console.error('[StorageCleanup] Failed to cleanup sessions:', error);
    return 0;
  }
}

/**
 * Get storage usage statistics
 */
export async function getStorageStats(): Promise<{
  sessionCount: number;
  pageCount: number;
  componentCount: number;
  menuCount: number;
  projectCount: number;
  estimatedSizeBytes: number;
}> {
  try {
    const [sessions, pages, components, menus, projects, projectFiles] = await Promise.all([
      db.sessions.count(),
      db.pages.count(),
      db.components.count(),
      db.menus.count(),
      db.projects.count(),
      db.projectFiles.count(),
    ]);
    
    // Rough estimate: average 1KB per session, 2KB per page, 500B per component
    const estimatedSizeBytes = 
      sessions * 1024 +
      pages * 2048 +
      components * 512 +
      menus * 1024 +
      projects * 512 +
      projectFiles * 4096;
    
    return {
      sessionCount: sessions,
      pageCount: pages,
      componentCount: components,
      menuCount: menus,
      projectCount: projects,
      estimatedSizeBytes,
    };
  } catch (error) {
    console.error('[StorageCleanup] Failed to get storage stats:', error);
    return {
      sessionCount: 0,
      pageCount: 0,
      componentCount: 0,
      menuCount: 0,
      projectCount: 0,
      estimatedSizeBytes: 0,
    };
  }
}

/**
 * Clear all storage data (for troubleshooting)
 */
export async function clearAllStorage(): Promise<boolean> {
  try {
    await db.transaction('rw', 
      [db.sessions, db.pages, db.components, db.menus, db.projects, db.projectFiles], 
      async () => {
        await db.projectFiles.clear();
        await db.projects.clear();
        await db.components.clear();
        await db.menus.clear();
        await db.pages.clear();
        await db.sessions.clear();
      }
    );
    
    console.log('[StorageCleanup] All storage cleared');
    return true;
  } catch (error) {
    console.error('[StorageCleanup] Failed to clear storage:', error);
    return false;
  }
}

/**
 * Export storage data for backup
 */
export async function exportStorageData(): Promise<{
  sessions: unknown[];
  pages: unknown[];
  components: unknown[];
  menus: unknown[];
  projects: unknown[];
  projectFiles: unknown[];
}> {
  const [sessions, pages, components, menus, projects, projectFiles] = await Promise.all([
    db.sessions.toArray(),
    db.pages.toArray(),
    db.components.toArray(),
    db.menus.toArray(),
    db.projects.toArray(),
    db.projectFiles.toArray(),
  ]);
  
  return {
    sessions,
    pages,
    components,
    menus,
    projects,
    projectFiles,
  };
}
